import { Component, OnInit,EventEmitter,Input,Output } from '@angular/core';
import { server } from '../server';

@Component({
  selector: 'app-server-item',
  // inputs : ['serverItem'],
  // outputs : ['showServerDetail'],
  templateUrl: './server-item.component.html',
  styleUrls: ['./server-item.component.css']
})
export class ServerItemComponent implements OnInit {

  @Input() serverItem: server;
  @Output() showServerDetail = new EventEmitter<server>();
  @Output() removeServerItem = new EventEmitter<server>();
  @Output() informRemove = new EventEmitter<boolean>();
  constructor() { 
    console.log(' item constrctor() called');
  }

  onShowDetail(item : server){
    this.showServerDetail.emit(item)
    this.informRemove.emit(true)
  }

  updateStatus() : void{
    this.serverItem.status = !this.serverItem.status;
  }

  onRemoveServer(item : server) {
    this.removeServerItem.emit(item)
  }

  ngOnInit(): void {
   console.log('item ngOnInit() callled');
  }

}
