import { Component, OnInit, ViewChild, ElementRef, OnChanges, SimpleChanges } from '@angular/core';
import { server } from './server'

@Component({
  selector: 'app-server',
  templateUrl: './server.component.html',
  styleUrls: ['./server.component.css']
})
export class ServerComponent implements OnInit {

  public serverDetailItem : server;
  @ViewChild('name') name : ElementRef;
  @ViewChild('check') check : ElementRef;
  @ViewChild('detail') detail : ElementRef;
  range : number;

  servers : server[] = [];

  constructor() {
    
   }

 
  onShowServerDetail(data : server) {
    this.serverDetailItem = data;
  
  }

  onRemoveServer(data : server) {
    const itemToRemove = this.servers.findIndex( item => item.name == data.name)
    this.servers.splice(itemToRemove,1)
    this.serverDetailItem = null;
  }

  onSubmit(){
  const isExist = this.servers.find(item => item.name == this.name.nativeElement.value)

  if(isExist){
    alert('already exist')
    return;
  }

  this.servers.push(new server(this.name.nativeElement.value,this.check.nativeElement.checked,this.detail.nativeElement.value))
  console.log(this.servers);
  }


  ngOnInit(): void {
   
  }

}
